<template>
  <div class="hc-no-data">
    <div>
      <svg class="icon" aria-hidden="true">
        <use :xlink:href="noDataIcon"></use>
      </svg>
    </div>
    <div>
      <span class="hc-data-text">{{dataText}}</span>
    </div>
    <div class="hc-data-btn">
      <mt-button>{{dataBtn}}</mt-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    dataText: {
      type: String,
      default: '暂无订单数据，块去购买商品吧'
    },
    dataBtn: {
      type: String,
      default: '我要购买商品'
    },
    noDataIcon: {
      type: String,
      default: '#icon-zu2'
    }
  }

}
</script>

<style lang="scss">
.hc-no-data {
  width: 100%;
  padding-top: 60px;
  text-align: center;
  .icon {
    text-align: center;
    font-size: 175px;
  }
  .hc-data-text {
    line-height: 60px;
    color: #bfbfbf;
    font-size: 14px;
  }
  .hc-data-btn {
    padding: 60px 0;
    button {
      width: 150px;
      height: 48px;
      color: #f3ab03;
      border-radius: 24px;
      -webkit-box-shadow: none;
      box-shadow: none;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.16) !important;
    }

  }
}
</style>



