<template>
  <page>
    <div slot="header" class="hc-clearfix hc-order-switch hc-c333">
      <mt-button class="hc-order-tab hc-c333 hc-col-3" :class="active==1?'active':''" size="small" @click.native.prevent="switchTab('1')">全部订单</mt-button>
      <mt-button class="hc-order-tab hc-c333 hc-col-3" :class="active==2?'active':''" size="small" @click.native.prevent="switchTab('2')">待接单</mt-button>
      <mt-button class="hc-order-tab hc-c333 hc-col-3" :class="active==3?'active':''" size="small" @click.native.prevent="switchTab('3')">配送中</mt-button>
      <mt-button class="hc-order-tab hc-c333 hc-col-3" :class="active==4?'active':''" size="small" @click.native.prevent="switchTab('4')">已完成</mt-button>
    </div>

    <div slot="content" class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" :swipeable="swipeable">
        <mt-tab-container-item id="1">
        	<!-- 全部订单 -->
          <div v-if="allOrderList.length !== 0">
            <OrderLabel v-for="(item, index) in allOrderList" :key="index" :status="'待接单'" />
          </div>
          <div v-else>
            <Nodate/>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
        	<!-- 待接单 -->
          <div v-if="waitOrderList.length !== 0">
            <OrderLabel v-for="(item, index) in waitOrderList" :key="index" :status="'待接单'" />
          </div>
          <div v-else>
            <Nodate/>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
          <!-- 配送中 -->
          <div v-if="onTheWayOrderList.length !== 0">
            <OrderLabel v-for="(item, index) in onTheWayOrderList" :key="index" :status="'配送中'" />
          </div>
          <div v-else>
            <Nodate/>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="4">
        	<!-- 已完成 -->
          <div v-if="completeOrderList.length !== 0">
            <OrderLabel v-for="(item, index) in completeOrderList" :key="index" :status="'已完成'" />
          </div>
          <div v-else>
            <Nodate/>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </page>
</template>

<script>
import { TabContainer, TabContainerItem } from 'mint-ui'
import OrderLabel from './components/orderLabel'
import Nodate from './components/noData'
export default {
  name: 'page-tab-container',
  data() {
    return {
      smg: true,
      active: '1',
      swipeable: false,
      allOrderList: new Array(3),
      waitOrderList: new Array(0),
      onTheWayOrderList: new Array(2),
      completeOrderList: new Array(1)

    };
  },
  components:{
    OrderLabel,
    Nodate
  },
  methods:{
    switchTab(index){
      this.active = index + '';
      console.log(this.active);
    }
  }
};
</script>

<style lang="scss">
.page-tabbar-tab-container {
  background-color: #f1f1f1;
  padding-top: 48px;
  overflow: scroll;
}
.smgsmg {
  overflow: scroll;
}
.hc-order-switch {
  position: fixed;
  z-index: 500;
  width: 100%;
  border-bottom: 1px solid #dbdbdb;
}
.hc-order-tab {
  border-radius: 0px !important;
  box-shadow: none !important;
  height: 48px !important;
  background-color: #fff !important;
  font-size: 14px !important;
  border-bottom: 3px solid transparent !important;

  &.active {
    color: #f3ab03;
    border-bottom: 3px solid #f3ab03;
  }
}
</style>



