<template>
  <div class="hc-order-label hc-c333">
    <div class="hc-order-label-item">
      <div @click="toOrderDetail(status)" class="hc-order-header">
        <div class="hc-order-number hc-one-in">
          订单号:E2018948374859
        </div>
        <div class="hc-order-price">
          <div>￥360.00</div>
          <div class="hc-order-status">{{status}}</div>
        </div>
      </div>
      <div @click="toOrderDetail(status)" class="hc-order-conent">
        <div class="hc-order-item">
          <div class="hc-order-commodity">
            <div class="hc-commodity-img">
              <img src="" alt="">
            </div>
            <div class="hc-commodity-desc">
              <div>
                <div class="hc-14">泰山原浆啤酒 七天鲜跃</div>
                <div class="hc-12 hc-c999">720ml*6瓶/件</div>
              </div>
            </div>
            <div class="hc-commodity-price">
              <div>￥90.00 X 2</div>
            </div>
          </div>
        </div>
        <div class="hc-order-item">
          <div class="hc-order-commodity">
            <div class="hc-commodity-img">
              <img src="" alt="">
            </div>
            <div class="hc-commodity-desc">
              <div>
                <div class="hc-14">泰山原浆啤酒 七天鲜跃</div>
                <div class="hc-12 hc-c999">720ml*6瓶/件</div>
              </div>
            </div>
            <div class="hc-commodity-price">
              <div>￥90.00 X 2</div>
            </div>
          </div>
        </div>
      </div>
      <div class="hc-order-foot">
        <svg class="icon hc-foot-img" aria-hidden="true">
          <use xlink:href="#icon-ziyuan11"></use>
        </svg>
        <div class="hc-foot-desc">
          <div>泰山啤酒银座商城店</div>
          <div>2018-7-31 14:30 送货上门</div>
        </div>
      </div>
      <div class="hc-order-btn hc-clearfix">
        <div class="hc-btn-cancel hc-col-6" @click="cancelOrder">{{buttonCancelText}}</div>
        <div class="hc-btn-phone hc-col-6"><a href="tel:13527783971" >{{buttonTextType}}</a></div>
      </div>
    </div>
  </div>
</template>
<script>
import { HcMessageBox } from '../../../components/common/hcMessage/index.js'
export default {
  props: {
    status: {
      type: String,
      default: '待接单'
    }
  },
  data() {
    return {
      // buttonTextType:'电话催单'
    }
  },
  computed: {
    buttonTextType(){
      if(this.status == '已完成') {
        return '再次购买'
      }else {
        return '电话催单'
      }
    },
    buttonCancelText(){
      if(this.status == '已完成') {
        return '删除订单'
      }else {
        return '取消订单'
      }
    }
  },
  methods: {
    cancelOrder() {
      if(this.status === '配送中'){
        HcMessageBox.confirm('配送中的订单无法取消')
      }else {
        HcMessageBox.alert(`确定${this.buttonCancelText}`)
      }
    },
    toOrderDetail(params) {
      this.$router.push({ name: "orderDetail" });
    }
  }
}
</script>
<style lang="scss">
.hc-order-label {
  background-color: #fff;
  margin-bottom: 10px;
  .hc-order-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 15px;
    height: 60px;
    border-bottom: 1px solid #dbdbdb;
    .hc-order-number {
      float: left;
    }
    .hc-order-price {
      text-align: center;
      float: right;
      .hc-order-status {
        color: #ff2a01;
      }
    }
  }
  .hc-order-item {
    padding: 15px;
    position: relative;
    .hc-order-commodity {
      display: flex;
      align-items: center;
      .hc-commodity-img {
        position: absolute;
        height: 40px;
        width: 40px;
        img {
          display: inline-block;
          width: 40px;
          height: 40px;
          background-color: #000;
        }
      }
      .hc-commodity-desc {
        display: flex;
        align-items: center;
        // min-height: 100px;
        padding: 0px 70px 0px 55px;
        line-height: 20px;
      }
      .hc-commodity-price {
        position: absolute;
        right: 15px;
      }
    }

  }
  .hc-order-foot {
    padding: 15px;
    position: relative;
    display: flex;
    align-items: center;
    .hc-foot-img {
      width: 30px;
      height: 30px;
      position: absolute;
      color: #78d7ab;
    }
    .hc-foot-desc {
      padding-left: 50px;
    }
  }
  .hc-order-btn {
    border-top: 1px solid #dbdbdb;
    line-height: 45px;
    padding: 0px 20px;
    text-align: center;
    background-color: #fff;
    font-size: 14px;
    .hc-btn-cancel {
      position: relative;
      color: #9e9e9e;
      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        width: 1px;
        height: 35px;
        right: 0px;
        bottom: 0px;
        top: 0px;
        margin: auto;
        background-color: #dbdbdb;
      }
    }
    .hc-btn-phone {
      color: #ff2a01;
      a {
        width: 100%;
        height: 100%;
        color: inherit;
        text-decoration: none;
      }
    }
  }
}
</style>





